const imgAdd= function(pos, $file){
  // ..
  let formdata = new FormData();
  formdata.append('smfile', $file);
  this.axios({
    url: 'https://sm.ms/api/v2/upload',
    method: 'post',
    data: formdata,
    headers: { 'Content-Type': 'multipart/form-data' },
  }).then((url) => {
    // .url![...](0) -> ![...](url)
    // $vm.$img2Url 
    // $vm.$img2Url(pos, url);
    this.$refs.md.$img2Url(pos, url.data.data.url)
  })

}

export {imgAdd}



